import React, { useState, useEffect } from 'react';
import {
  Grid,
  Paper,
  Typography,
  Box,
  Card,
  CardContent,
  CardActions,
  Button,
  Chip,
  LinearProgress,
  IconButton,
  Tooltip
} from '@mui/material';
import {
  TrendingUp,
  Description,
  CloudUpload,
  Timer,
  CheckCircle,
  Error as ErrorIcon,
  Refresh,
  ArrowForward,
  Psychology,
  Speed
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { formatDistanceToNow } from 'date-fns';
import api from '../services/api';
import Loading from '../components/common/Loading';
import toast from 'react-hot-toast';

const StatCard = ({ title, value, icon, color = 'primary', trend }) => (
  <Paper sx={{ p: 3 }}>
    <Box display="flex" alignItems="center" justifyContent="space-between">
      <Box>
        <Typography color="text.secondary" variant="body2" gutterBottom>
          {title}
        </Typography>
        <Typography variant="h4" fontWeight="bold">
          {value}
        </Typography>
        {trend && (
          <Box display="flex" alignItems="center" mt={1}>
            <TrendingUp fontSize="small" color="success" />
            <Typography variant="body2" color="success.main" ml={0.5}>
              {trend}% from last week
            </Typography>
          </Box>
        )}
      </Box>
      <Box
        sx={{
          backgroundColor: `${color}.light`,
          borderRadius: 2,
          p: 1.5,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center'
        }}
      >
        {icon}
      </Box>
    </Box>
  </Paper>
);

const RecentSummaryCard = ({ summary, onView }) => {
  const getStatusColor = (status) => {
    switch (status) {
      case 'completed': return 'success';
      case 'processing': return 'info';
      case 'failed': return 'error';
      default: return 'default';
    }
  };

  const getProviderIcon = (provider) => {
    switch (provider) {
      case 'openai': return '🤖';
      case 'gemini': return '✨';
      default: return '📄';
    }
  };

  return (
    <Card sx={{ height: '100%' }}>
      <CardContent>
        <Box display="flex" justifyContent="space-between" alignItems="start" mb={1}>
          <Typography variant="h6" noWrap sx={{ maxWidth: '70%' }}>
            {summary.title}
          </Typography>
          <Chip
            label={summary.status}
            color={getStatusColor(summary.status)}
            size="small"
          />
        </Box>
        
        <Typography variant="body2" color="text.secondary" paragraph>
          {summary.preview || 'Processing...'}
        </Typography>

        <Box display="flex" gap={1} flexWrap="wrap">
          <Chip
            icon={<Typography>{getProviderIcon(summary.provider)}</Typography>}
            label={summary.provider}
            size="small"
            variant="outlined"
          />
          <Chip
            label={summary.sourceType}
            size="small"
            variant="outlined"
          />
          <Typography variant="caption" color="text.secondary" sx={{ ml: 'auto' }}>
            {formatDistanceToNow(new Date(summary.createdAt), { addSuffix: true })}
          </Typography>
        </Box>

        {summary.status === 'processing' && summary.progress && (
          <Box mt={2}>
            <LinearProgress variant="determinate" value={summary.progress} />
            <Typography variant="caption" color="text.secondary">
              {summary.progress}% complete
            </Typography>
          </Box>
        )}
      </CardContent>
      
      <CardActions>
        <Button 
          size="small" 
          onClick={() => onView(summary.id)}
          disabled={summary.status !== 'completed'}
          endIcon={<ArrowForward />}
        >
          View Summary
        </Button>
      </CardActions>
    </Card>
  );
};

const Dashboard = () => {
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({
    totalSummaries: 0,
    thisWeek: 0,
    processingTime: 0,
    successRate: 0
  });
  const [recentSummaries, setRecentSummaries] = useState([]);
  const [refreshing, setRefreshing] = useState(false);
  
  const { user } = useAuth();
  const navigate = useNavigate();

  const fetchDashboardData = async () => {
    try {
      const [statsRes, summariesRes] = await Promise.all([
        api.get('/api/summaries/stats'),
        api.get('/api/summaries?limit=6')
      ]);

      setStats(statsRes.data.data);
      setRecentSummaries(summariesRes.data.data.summaries);
    } catch (error) {
      console.error('Failed to fetch dashboard data:', error);
      toast.error('Failed to load dashboard data');
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  useEffect(() => {
    fetchDashboardData();
    
    // Refresh every 30 seconds for live updates
    const interval = setInterval(fetchDashboardData, 30000);
    return () => clearInterval(interval);
  }, []);

  const handleRefresh = () => {
    setRefreshing(true);
    fetchDashboardData();
  };

  const handleViewSummary = (summaryId) => {
    navigate(`/summaries/${summaryId}`);
  };

  if (loading) {
    return <Loading message="Loading dashboard..." />;
  }

  return (
    <Box>
      {/* Welcome Section */}
      <Box mb={4}>
        <Typography variant="h4" gutterBottom>
          Welcome back, {user?.name}!
        </Typography>
        <Typography variant="body1" color="text.secondary">
          Here's an overview of your AI-powered content summaries
        </Typography>
      </Box>

      {/* Stats Grid */}
      <Grid container spacing={3} mb={4}>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Total Summaries"
            value={stats.totalSummaries}
            icon={<Description color="primary" />}
            color="primary"
          />
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="This Week"
            value={stats.thisWeek}
            icon={<TrendingUp color="success" />}
            color="success"
            trend={12}
          />
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Avg. Processing"
            value={`${stats.processingTime}s`}
            icon={<Speed color="info" />}
            color="info"
          />
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Success Rate"
            value={`${stats.successRate}%`}
            icon={<CheckCircle color="success" />}
            color="success"
          />
        </Grid>
      </Grid>

      {/* Quick Actions */}
      <Paper sx={{ p: 3, mb: 4 }}>
        <Typography variant="h6" gutterBottom>
          Quick Actions
        </Typography>
        <Box display="flex" gap={2} flexWrap="wrap" mt={2}>
          <Button
            variant="contained"
            startIcon={<CloudUpload />}
            onClick={() => navigate('/upload')}
            size="large"
          >
            Upload New Content
          </Button>
          <Button
            variant="outlined"
            startIcon={<Psychology />}
            onClick={() => navigate('/upload?type=url')}
          >
            Summarize from URL
          </Button>
          <Button
            variant="outlined"
            startIcon={<Description />}
            onClick={() => navigate('/summaries')}
          >
            View All Summaries
          </Button>
        </Box>
      </Paper>

      {/* Recent Summaries */}
      <Box>
        <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
          <Typography variant="h6">
            Recent Summaries
          </Typography>
          <Tooltip title="Refresh">
            <IconButton onClick={handleRefresh} disabled={refreshing}>
              <Refresh />
            </IconButton>
          </Tooltip>
        </Box>
        
        {recentSummaries.length === 0 ? (
          <Paper sx={{ p: 4, textAlign: 'center' }}>
            <Typography variant="body1" color="text.secondary" paragraph>
              No summaries yet. Upload your first content to get started!
            </Typography>
            <Button
              variant="contained"
              startIcon={<CloudUpload />}
              onClick={() => navigate('/upload')}
            >
              Upload Content
            </Button>
          </Paper>
        ) : (
          <Grid container spacing={3}>
            {recentSummaries.map((summary) => (
              <Grid item xs={12} md={6} lg={4} key={summary.id}>
                <RecentSummaryCard
                  summary={summary}
                  onView={handleViewSummary}
                />
              </Grid>
            ))}
          </Grid>
        )}
      </Box>
    </Box>
  );
};

export default Dashboard;